.cms-menu.collapsed .cms-menu-list {
  float: left;
}

.cms-menu.collapsed .cms-fluent-selector .text {
  display: none;
}

.cms-menu.collapsed .cms-fluent-selector-locales {
  width: 100%;
}

.cms-menu.collapsed .cms-fluent-selector-locales .full-title {
  display: none;
}

.cms-menu.collapsed .cms-fluent-selector-locales .short-title {
  display: block;
}

.cms-fluent-selector {
  display: block;
  height: auto;
  line-height: 24px;
  overflow: visible;
  padding: 5px;
  position: relative;
}

.cms-fluent-selector:before {
  color: white;
  position: absolute;
  top: 50%;
  line-height: 0;
  right: 24px;
  width: 0;
}

.cms-panel-layout.collapsed .cms-fluent-selector:before {
  right: 36px;
}

.cms-fluent-selector .cms-fluent-selector-locales {
  background-color: #368b39;
  background-image: url("../images/menu-icon-24-white.png");
  background-position: 6px center;
  background-repeat: none;
  background-size: 12px;
  border-radius: 5px;
  border: 0 none;
  color: white;
  padding: 5px 21px 5px 24px;
  width: 100%;
}

.cms-panel-layout.collapsed .cms-fluent-selector .cms-fluent-selector-locales {
  color: transparent;
  padding: 2px;
}

.field.LocalisedField label.left:after {
  float: left;
  content: url("../images/menu-icon-16-green.png");
  display: block;
  width: 16px;
  height: 16px;
  padding: 0 6px 0 0;
  opacity: 0.7;
}

.field.LocalisedField label.left span.fluent-locale-label {
  color: #FFFFFF;
  background: #1f9433;
  display: inline-block;
  padding: 3px 3px 1px 3px;
  margin-right: 6px;
  border-radius: 2px;
  font-size: 9px;
  text-transform: uppercase;
  font-weight: 100;
  text-shadow: none;
  line-height: 1;
  cursor: default;
}

.field.LocalisedField label.left span.fluent-locale-label.fluent-modified-value {
  background: #f49100;
}

.field.LocalisedField label.left span.fluent-locale-label.fluent-modified-value:hover {
  cursor: pointer;
  background: #1f9433;
}

.jstree li.status-fluenthidden .item {
  opacity: 0.5;
}

span.badge.status-fluenthidden {
  color: white;
  border: 1px solid #0d4016;
  background-color: #1f9433;
  text-shadow: none;
}




